.transaction {
  border-spacing: 0 10px;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
}

.title {
  padding: 10px 20px;
  background: #56433d;
  color: #f9c941;
  border-right: 2px solid;
  font-size: 0.9em;
}

.title:first-child {
  text-align: left;
}

.title:last-child {
  border-right: none;
}

.value {
  vertical-align: middle;
  padding: 10px;
  font-size: 14px;
  text-align: center;
  border-top: 2px solid #56433d;
  border-bottom: 2px solid #56433d;
  border-right: 2px solid #56433d;
}
.value:first-child {
  border-left: 2px solid #56433d;
  border-right: none;
}
.value:nth-child(2) {
  text-align: left;
}
